/* eslint-disable camelcase */
/* eslint-disable prefer-promise-reject-errors */
import React, { useState } from 'react';
import {
  Button,
  FormGroup,
  Input,
  Label,
  ModalBody,
  ModalFooter,
  ModalHeader,
} from 'reactstrap';
import instance from '../../helpers/axiosInstance';
import IntlMessages from '../../helpers/IntlMessages';
import { getCurrentUser } from '../../helpers/Utils';
import StateButton from '../StateButton';

const AddFaqModal = ({ setOpenAddModal, id }) => {
  const [formValues, setFormValues] = useState({
    hackathon_id: id,
    question: '',
    answer: '',
  });

  const { access_token } = getCurrentUser();

  const handleChange = (event, key, value = false) => {
    setFormValues({
      ...formValues,
      [key]: value ? event : event.target.value,
    });
  };

  const handleSubmit = async (newData) => {
    return new Promise((success, failed) => {
      const addJudge = async () => {
        try {
          const response = await instance({
            method: 'POST',
            url: '/addFaqs',
            data: { faqs: { ...newData } },
            headers: {
              Authorization: `Bearer ${access_token}`,
            },
          });

          const { data } = response;
          if (data.success) {
            setFormValues({ ...formValues, ...newData });
            success('Hackathon updated successfully!');
          } else {
            failed('Failed to update hackathon!');
          }
        } catch (error) {
          console.log(error);
          failed('Failed to update hackathon!');
        }
      };

      addJudge();
    });
  };

  return (
    <>
      <ModalHeader>
        <h2 className="lead">
          <IntlMessages id="challenges.modal.application-add-faq-title" />
        </h2>
      </ModalHeader>

      <ModalBody>
        <FormGroup>
          <Label>
            <IntlMessages id="challenges.modal.faq" />
          </Label>
          <Input
            type="text"
            value={formValues.question}
            onChange={(event) => handleChange(event, 'question')}
          />
        </FormGroup>

        <FormGroup>
          <Label>
            <IntlMessages id="challenges.modal.faq-description" />
          </Label>
          <Input
            type="textarea"
            rows="6"
            value={formValues.answer}
            onChange={(event) => handleChange(event, 'answer')}
          />
        </FormGroup>
      </ModalBody>

      <ModalFooter>
        <StateButton
          id="add-faq-button"
          color="secondary"
          onClick={() => handleSubmit(formValues)}
          // next={() => window.location.reload()}
        >
          <IntlMessages id="challenges.modal.application-add" />
        </StateButton>
        <Button color="danger" onClick={() => setOpenAddModal(false)}>
          <IntlMessages id="challenges.modal.application-cancel" />
        </Button>
      </ModalFooter>
    </>
  );
};

export default AddFaqModal;
