import React, { useState } from 'react';
import {
  Button,
  Card,
  CardBody,
  CustomInput,
  FormGroup,
  Modal,
  Row,
} from 'reactstrap';
import AddFaqModal from '../../../components/application/AddFaqModal';
import { Colxx, Separator } from '../../../components/common/CustomBootstrap';
import { adminRoot } from '../../../constants/defaultValues';

import Breadcrumb from '../../../containers/navs/Breadcrumb';
import AppError from '../../../helpers/AppError';
import IntlMessages from '../../../helpers/IntlMessages';

import FaqList from '../../../components/application/FaqList';

const Faq = ({ match }) => {
  const { id } = match.params;

  if (!id) throw new AppError('Please provide a Hackathon ID');

  const [openAddModal, setOpenAddModal] = useState(false);

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.faq" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>

      <Row className="justify-content-center">
        <Colxx xxs="12" md="6">
          <FaqList id={id} />
        </Colxx>

        <Colxx xxs="12" md="6">
          <Card>
            <CardBody>
              <Row className="justify-content-center m-2">
                <Button onClick={() => setOpenAddModal(true)}>
                  <IntlMessages id="challenges.add-faq-button" />
                </Button>
              </Row>

              <Row className="justify-content-center m-2">
                <CustomInput
                  type="checkbox"
                  label={<IntlMessages id="challenges.add-faq-checkbox" />}
                />
              </Row>

              <Modal
                isOpen={openAddModal}
                toggle={() => setOpenAddModal(!openAddModal)}
              >
                <AddFaqModal setOpenAddModal={setOpenAddModal} id={id} />
              </Modal>
            </CardBody>
          </Card>
        </Colxx>
      </Row>

      <Row>
        <Colxx>
          <FormGroup className="text-center">
            <Button
              color="primary"
              className="m-3"
              href={`${adminRoot}/challenges/sponsors/${id}`}
            >
              <IntlMessages id="forms.previous" />
            </Button>
          </FormGroup>
        </Colxx>
      </Row>
    </>
  );
};

export default Faq;
